/* Projects Section */
#projects {
  padding: 40px 20px;
  margin: 1.2em 0 5em 0;
}

#projects h2 {
  font-size: 2.5rem;
  margin-bottom: 2em;
  text-align: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Tab Navigation */
.tabs {
  display: flex;
  margin-bottom: 20px;
  flex-direction: column;
  width: max-content;
  margin: auto;
}

.tab-button {
  background: linear-gradient(135deg, #f0f8ff 0%, #e6f2ff 100%);
  color: #333;
  padding: 16px 28px;
  font-size: 1.1rem;
  font-weight: 500;
  border: 2px solid #d0e4f7;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  border-radius: 8px;
  margin: 0.5em auto;
  min-width: 280px;
  max-width: 90%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  position: relative;
}

.tab-button:hover {
  background: linear-gradient(135deg, #2aacbd 0%, #1a8a99 100%);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(42, 172, 189, 0.3);
  border-color: #1a8a99;
}

.tab-button.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: #764ba2;
  box-shadow: 0 4px 16px rgba(102, 126, 234, 0.4);
}

/* Featured Tab Styling */
.tab-button.featured-tab {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  border: 2px solid #f5576c;
  color: white;
  font-weight: 600;
  animation: pulse 2s ease-in-out infinite;
}

.tab-button.featured-tab:hover {
  background: linear-gradient(135deg, #f5576c 0%, #f093fb 100%);
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(245, 87, 108, 0.4);
}

.tab-button.featured-tab.active {
  background: linear-gradient(135deg, #f5576c 0%, #c44569 100%);
  animation: none;
}

/* Sub-component Tab Styling */
.tab-button.sub-component {
  margin: auto;
  min-width: 260px;
  font-size: 1rem;
  padding: 12px 24px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border-left: 4px solid #667eea;
}

.tab-button.sub-component:hover {
  background: linear-gradient(135deg, #e9ecef 0%, #dee2e6 100%);
  border-left-color: #764ba2;
}

.tab-button.sub-component.active {
  background: linear-gradient(135deg, #495057 0%, #343a40 100%);
  border-left-color: #667eea;
  color: white;
}

.featured-badge {
  display: inline-block;
  background: rgba(255, 255, 255, 0.3);
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 0.75rem;
  margin-right: 8px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 0 4px 16px rgba(245, 87, 108, 0.4);
  }
  50% {
    box-shadow: 0 4px 24px rgba(245, 87, 108, 0.6);
  }
}

/* Content Sections */
.tab-content {
  display: none;
  animation: fadeIn 0.4s ease-in;
}

.tab-content.active {
  display: block;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Project Header */
.project-header {
  display: flex;
  align-items: center;
  gap: 1em;
  margin-bottom: 1.5em;
  flex-wrap: wrap;
}

.project-header h3 {
  font-size: 2rem;
  margin: 0;
  color: #2c3e50;
  text-decoration: none;
  border-bottom: 3px solid #667eea;
  padding-bottom: 0.3em;
  display: inline-block;
}

.featured-label {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: white;
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 8px rgba(245, 87, 108, 0.3);
}

/* Project Description */
.project-description {
  background: #f8f9fa;
  padding: 1.5em;
  border-radius: 8px;
  margin-bottom: 1.5em;
  border-left: 4px solid #667eea;
  line-height: 1.7;
}

.project-description p {
  margin-bottom: 1em;
  font-size: 1.05rem;
  color: #34495e;
}

.project-description p:last-child {
  margin-bottom: 0;
}

/* Section Headers */
.tech-stack-section h4,
.features-section h4,
.architecture-section h4,
.api-section h4,
.game-details-section h4,
.implementation-section h4,
.status-section h4,
.preview-section h4,
.video-section h4,
.screenshots-section h4,
.embed-section h4 {
  font-size: 1.4rem;
  color: #2c3e50;
  margin-top: 2em;
  margin-bottom: 0.8em;
  padding-bottom: 0.5em;
  border-bottom: 2px solid #e0e0e0;
}

/* Tech Stack */
.tech-stack-content {
  background: linear-gradient(135deg, #667eea15 0%, #764ba215 100%);
  padding: 1em 1.2em;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-size: 0.95rem;
  color: #2c3e50;
  border: 1px solid #d0d0d0;
  line-height: 1.6;
}

/* Features List */
.features-list,
.architecture-list,
.api-list,
.game-details-list,
.implementation-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.features-list li,
.architecture-list li,
.api-list li,
.game-details-list li,
.implementation-list li {
  padding: 0.8em 0;
  padding-left: 2em;
  position: relative;
  line-height: 1.6;
  color: #34495e;
  font-size: 1rem;
  border-bottom: 1px solid #f0f0f0;
}

.features-list li:last-child,
.architecture-list li:last-child,
.api-list li:last-child,
.game-details-list li:last-child,
.implementation-list li:last-child {
  border-bottom: none;
}

