.brackets-nav-vertical {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* buttons stack vertically */
  gap: 10px;
}

.tab-button {
  background: transparent;
  border: 1px solid #aaa;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
  text-align: left;
  z-index: 5;
  position: relative;
}

.tab-button.active {
  font-weight: bold;
}

.brackets {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  pointer-events: none;
  transition: all 0.3s ease; /* ðŸ”¥ smooth animation */
}

.bracket {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  transition: color 0.3s ease;
}
