.device-frame {
    width: 320px;
    height: 600px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    margin: 20px auto;
}

.mobile-preview {
    position: absolute;
    top: 50px;   /* adjust so it matches the screen area of your device.png */
    left: 42px;
    width: 234px;
    height: 500px; /* extended height so full app screen shows */
    z-index: 10;
    overflow: hidden;

}

.preview-image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
}

/* Arrows */
.arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.4);
    color: white;
    border: none;
    font-size: 20px;
    padding: 10px;
    cursor: pointer;
    z-index: 20;
    border-radius: 50%;
    transition: 'background' 0.2s;
}

.arrow:hover {
    background: rgba(0, 0, 0, 0.7);
}

.arrow.left {
    left: -20px; /* sit outside the device frame */
}

.arrow.right {
    right: -20px;
}
